import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
// form
import { Icon } from '@iconify/react';
// @mui
import {
  Box, Stack, Button, Divider, Typography, Table, TextField, Grid, Card, TableBody, TableRow, TableHead, TableCell
} from '@mui/material';
// utils
import Iconify from '../../../../components/iconify';
// components

// ----------------------------------------------------------------------

// ----------------------------------------------------------------------

InvoiceNewEditDetails.propTypes = {
  data: PropTypes.object,
};

export default function InvoiceNewEditDetails({ data }) {

  const [orderData, setOrderData] = useState({
    discount: 0,
    discountAmount: 0,
    orderValue: 0,
  });

  const setDiscount = (value) => {
    setOrderData({
      ...orderData,
      discount: value,
      discountAmount: (Number(data?.order_total) * value) / 100,
      orderValue: (Number(data?.order_total) - ((Number(data?.order_total) * value) / 100)),
    });
  }



  useEffect(() => {
    setOrderData({ ...orderData, orderValue: data?.order_total });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  console.log(data);

  const paperPrice = (sheetValue, paperValue) => ((sheetValue * paperValue) / 100) + sheetValue;

  const fCurrency = (sign, val) => `${sign} ${val}`

  return (
    <Box sx={{ p: 4 }}>
      <Typography variant="h6" sx={{ color: 'text.', mb: 3 }}>
        Details :
      </Typography>

      <Stack divider={<Divider flexItem sx={{ borderStyle: 'dashed' }} />} spacing={3}>
        <Stack alignItems="flex-end" spacing={1.5}>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={4}>
                <TextField
                  size="small"
                  label="Porduct Name"
                  value={data?.product?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ color: 'red', marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label="Orientation"
                  value={data?.productorientation?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label="Size"
                  value={data?.productsize?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}

                />
              </Grid>
            </Grid>
          </Stack>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={4}>
                <TextField
                  size="small"
                  label="Paper Sheet"
                  value={data?.productsheet?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ color: 'red', marginBottom: '1em' }}

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label="Paper Type"
                  value={data?.productpaper?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label="Print Pages Quantity"
                  value={data?.page_qty}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}

                />
              </Grid>
            </Grid>
          </Stack>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={4}>
                <TextField
                  size="small"
                  label="Album Cover"
                  value={data?.productcover?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ color: 'red', marginBottom: '1em' }}

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label=" Leather option"
                  value={data?.coversupgrade?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label="Cover Option Color"
                  value={data?.coversupgradecolor?.color}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label="Cover Type"
                  value={data?.productcover?.type}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth

                />
              </Grid>
              {
                data?.productcover?.type === 'img' &&
                <Grid item xs={12} md={2}>
                  <Button variant="outlined" fullWidth startIcon={<Icon icon="mdi:download" />}>
                    Download Photo
                  </Button>
                </Grid>
              }
            </Grid>
          </Stack>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={4}>
                <TextField
                  size="small"
                  label="Box or Sleeve"
                  value={data?.productboxsleeve?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ color: 'red' }}

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label=" Box or Sleeve option"
                  value={data?.coversupgrade?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label="Box or Sleeve Option Color"
                  value={data?.productboxsleeve_upgrade_color?.color}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth

                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  label="Box or Sleeve Type"
                  value={data?.productcover?.type}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth

                />
              </Grid>

            </Grid>
          </Stack>
        </Stack>

      </Stack>

      <Divider sx={{ my: 3, borderStyle: 'dashed' }} />

      {

      }

      <Typography variant="h6" sx={{ color: 'text.' }}>
        Printing Data :
      </Typography>
      <Grid container spacing={2} display='flex' alignItems='center' sx={{ mb: 5 }} >
        {
          data?.productcover?.type !== "option_colors" && (
            <>
              {
                data?.coverfrontimg ? (
                  <Grid item xs={6} md={3} sx={{ my: 3 }} >
                    <Button href={data?.coverfrontimg} download={1} variant="contained" fullWidth startIcon={<Icon icon="mdi:download" />}>
                      Covers front photo
                    </Button>
                  </Grid>
                ) : (
                  <Grid item xs={6} md={3} sx={{ my: 3 }}>
                    <Typography>No Data : Covers front photo</Typography>
                  </Grid>
                )
              }
              {
                data?.coverbacksideimg ? (
                  <Grid item xs={6} md={3} sx={{ my: 3 }}>
                    <Button download href={data?.coverbacksideimg} variant="contained" fullWidth startIcon={<Icon icon="mdi:download" />}>
                      Cover back photo
                    </Button>
                  </Grid>
                ) : (
                  <Grid item xs={6} md={3} sx={{ my: 3 }}>
                    <Typography>No Data : Cover back photo</Typography>
                  </Grid>
                )
              }
            </>
          )
        }

        {
          data?.productcover?.type !== "option_colors" && (
            <>
              {
                data?.boxsleevefrontimg ? (
                  <Grid item xs={6} md={3} sx={{ my: 3 }}>
                    <Button download href={data?.boxsleevefrontimg} variant="contained" fullWidth startIcon={<Icon icon="mdi:download" />}>
                      Box or sleve front photo
                    </Button>
                  </Grid>
                ) : (
                  <Grid item xs={6} md={3} sx={{ my: 3 }}>
                    <Typography>No Data : Box or sleve front photo</Typography>
                  </Grid>
                )
              }
              {
                data?.boxsleevebacksideimg ? (
                  <Grid item xs={6} md={3} sx={{ my: 3 }}>
                    <Button download href={data?.boxsleevebacksideimg} variant="contained" fullWidth startIcon={<Icon icon="mdi:download" />}>
                      Box or sleve back photo
                    </Button>
                  </Grid>
                ) : (
                  <Grid item xs={6} md={3} sx={{ my: 3 }}>
                    <Typography>No Data : Box or sleve back photo</Typography>
                  </Grid>
                )
              }
            </>
          )
        }
        {
          data?.order_photos_link ? (
            <Grid item xs={6} md={4} sx={{ my: 3 }}>
              <Button download href={data?.order_photos_link?.source_link} variant="contained" fullWidth startIcon={<Icon icon="mdi:download" />}>
                Download Photo Stack
              </Button>
            </Grid>
          ) : (
            <Grid item xs={6} md={3} sx={{ my: 3 }}>
              <Typography>No photo stack data for printing</Typography>
            </Grid>
          )
        }
      </Grid>

      <Grid container spacing={2}>
        <Grid item xs={12} md={12}>
          <Table style={{ width: '100%' }} >
            {/* <TableBody style={{ width: '100%' }}>
            </TableBody> */}
            <TableHead style={{ width: '100%' }} >
              <TableRow style={{ width: '100%' }} >
                <TableCell>Event Type</TableCell>
                <TableCell align="right">Event Name</TableCell>
                <TableCell align="right">Event Date</TableCell>
                <TableCell align="right">Imprinting</TableCell>
                <TableCell align="right">Fonts</TableCell>
              </TableRow>
            </TableHead>
            <TableRow style={{ width: '100%' }} >
              <TableCell>{data?.order_detaild?.event_type}</TableCell>
              <TableCell align="right">{data?.order_detaild?.event_name}</TableCell>
              <TableCell align="right">{data?.order_detaild?.event_date}</TableCell>
              <TableCell align="right">{data?.order_detaild?.Imprinting}</TableCell>
              <TableCell align="right">{data?.order_detaild?.fonts}</TableCell>
            </TableRow>
          </Table>
        </Grid>
        <Grid item xs={12} md={12}>
          <TextField name="description" label="Customize Message" multiline rows={3} cols="30" fullWidth value={data?.order_detaild?.customizeMessage} InputLabelProps={{ style: { fontSize: '.875rem' } }} readOnly />
        </Grid>
      </Grid>
    </Box>
  );
}
