/* eslint-disable no-unsafe-optional-chaining */
/* eslint-disable object-shorthand */
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
// form
import { LoadingButton } from '@mui/lab';
// @mui
import { Box, Stack, Button, Divider, Typography, InputAdornment, TextField, Grid } from '@mui/material';
import { useDispatch } from 'react-redux';
import { api } from '../../../../Api/api';
import { fetchOrder } from '../../../../redux/slices/order';
// utils

// components

// ----------------------------------------------------------------------

// ----------------------------------------------------------------------

InvoiceNewEditDetails.propTypes = {
  data: PropTypes.object,
};

const productTotalPrice = (
  perAlbumCost,
  albumQty,
  printbind,
  photobook,
  discount = 0
) => {

  const discountValue = (perAlbumCost * discount) / 100;
  const perAlbumCostDiscount = perAlbumCost - discountValue;
  console.log(perAlbumCostDiscount);
  const totaleAlbumCost = perAlbumCostDiscount * albumQty
  const subtotale = totaleAlbumCost + printbind + photobook

  return {
    discountValue: discountValue,
    perAlbumCostDiscount: perAlbumCostDiscount,
    totaleAlbumCost: totaleAlbumCost,
    subtotale: subtotale,
    discount: subtotale,
  };
};

export default function InvoiceNewEditDetails({ data }) {

  const [orderData, setOrderData] = useState({
    discountValue: 0,
    perAlbumCostDiscount: 0,
    totaleAlbumCost: 0,
    subtotale: 0,
    discount: 0
  });
  const [loadingSave, setLoadingSave] = useState(false)

  const dispatch = useDispatch()
  const setDiscount = (value) => {
    const total = productTotalPrice(data?.album_cost, data?.album_qty, data?.pritnig_price * data?.page_qty, data?.album_book_copy_price * data?.album_book_copy_qty, value)
    setOrderData({
      ...orderData,
      discount: value,
      discountValue: total?.discountValue,
      perAlbumCostDiscount: total?.perAlbumCostDiscount,
      totaleAlbumCost: total?.totaleAlbumCost,
      subtotale: total?.subtotale,
    });
  }
  // album_qty
  // album_cost
  // album_discount_amountCost
  // album_after_discount_cost
  // album_total_cost
  // subtotale
  // order_total
  useEffect(() => {
    const total = productTotalPrice(data?.album_cost, data?.album_qty, data?.pritnig_price * data?.page_qty, data?.album_book_copy_price * data?.album_book_copy_qty, data?.discount)
    setOrderData({
      ...orderData,
      discount: data?.discount,
      discountValue: total?.discountValue,
      perAlbumCostDiscount: total?.perAlbumCostDiscount,
      totaleAlbumCost: total?.totaleAlbumCost,
      subtotale: total?.subtotale,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);


  const paperPrice = (sheetValue, paperValue) => ((sheetValue * paperValue) / 100) + sheetValue;

  const fCurrency = (sign, val) => `${sign} ${val}`

  console.log(orderData);

  return (
    <Box sx={{ p: 4 }}>
      <Typography variant="h6" sx={{ color: 'text.disabled', mb: 3 }}>
        Billing Details :
      </Typography>

      <Stack divider={<Divider flexItem sx={{ borderStyle: 'dashed' }} />} spacing={3}>
        <Stack alignItems="flex-end" spacing={1.5}>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={5}>
                <TextField
                  size="small"
                  label="Porduct Name"
                  value={data?.product?.name}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  type="number"
                  label="Quantity"
                  value="1"
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  size="small"
                  type="number"
                  label="Price"
                  value="0"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  disabled
                  size="small"
                  label="Total"
                  value="0"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
            </Grid>
          </Stack>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={5}>
                <TextField
                  size="small"
                  label="Paper Sheets & Paper Type"
                  value={`${data?.productsheet?.name} + ( ${data?.productpaper?.name} ${data?.productpaper?.value}%)`}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  type="number"
                  label="Page Quantity"
                  value={data?.page_qty}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  size="small"
                  label="Price"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  value={`${data?.sheetValue} + ${data?.productpaper?.value}% ( ${paperPrice(data?.sheetValue, data?.productpaper?.value)} )`}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  disabled
                  size="small"
                  label="Total"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  value={Number(paperPrice(data?.sheetValue, data?.productpaper?.value)) * Number(data?.page_qty)}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
            </Grid>
          </Stack>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={5}>
                <TextField
                  size="small"
                  label="Cover"
                  value={`${data?.productcover?.name}`}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  type="number"
                  label="Quantity"
                  value={1}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  size="small"
                  type="number"
                  label="Price"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  value={data?.coverValue}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  disabled
                  size="small"
                  label="Total"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  value={data?.coverValue}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
            </Grid>
          </Stack>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={5}>
                <TextField
                  size="small"
                  label="Box & Sleeve"
                  value={`${data?.productboxsleeve?.name}`}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  type="number"
                  label="Quantity"
                  value={1}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  size="small"
                  type="number"
                  label="Price"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  value={data?.boxSleeveValue}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  disabled
                  size="small"
                  label="Total"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  value={data?.boxSleeveValue}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
            </Grid>
          </Stack>
        </Stack>
        {data?.pritnig_price_type ===
          "design_print_bind" ? (

          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2} sx={{ width: 1 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={5}>
                <TextField
                  size="small"
                  label="Design + Print + Bind"
                  value='Design + Print + Bind'
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  size="small"
                  type="number"
                  label="Quantity"
                  value={data?.page_qty}
                  InputLabelProps={{ shrink: true, style: { fontSize: '.875rem' } }}
                  fullWidth
                  sx={{ marginBottom: '1em' }}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <TextField
                  size="small"
                  type="number"
                  label="Price"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  value={data?.pritnig_price}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  disabled
                  size="small"
                  label="Total"
                  InputLabelProps={{ style: { fontSize: '.875rem' } }}
                  value={(data?.pritnig_price *
                    data?.page_qty)}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">{data?.countryzone?.currency_sign}</InputAdornment>,
                  }}
                  fullWidth
                />
              </Grid>
            </Grid>
          </Stack>
        ) : null}

      </Stack>

      <Divider sx={{ my: 3, borderStyle: 'dashed', borderColor: '#b4b4b4' }} />

      <Stack
        spacing={2}
        direction={{ xs: 'column-reverse', md: 'row' }}
        alignItems={{ xs: 'flex-start', md: 'center' }}
      >

        <Stack
          spacing={2}
          justifyContent="flex-end"
          direction={{ xs: 'column', md: 'row' }}
          sx={{ width: 1 }}
        >
          <TextField
            size="small"
            label="Discount"
            value={orderData.discount}
            type='number'
            InputLabelProps={{ style: { fontSize: '.875rem' } }}
            onChange={(event) => setDiscount(Number(event.target.value))}
            InputProps={{
              startAdornment: <InputAdornment position="start">%</InputAdornment>,
            }}
            sx={{ maxWidth: { md: 200 } }}
          />
        </Stack>
      </Stack>
      <Stack spacing={2} sx={{ mt: 3 }}>
        <Stack direction="row" justifyContent="flex-end">
          <Typography>Per Album Price  :</Typography>
          <Typography sx={{ textAlign: 'right', width: 120 }}>
            {fCurrency(data?.countryzone?.currency_sign, Math.round(data?.album_cost)) || '-'}
          </Typography>
        </Stack>
        <Stack direction="row" justifyContent="flex-end">
          <Typography>Discount {`( ${orderData.discount}% )`} :</Typography>
          <Typography color='error' sx={{ textAlign: 'right', width: 120 }}>
            - {fCurrency(data?.countryzone?.currency_sign, Math.round(orderData?.discountValue)) || '-'}
          </Typography>
        </Stack>
        <Stack direction="row" justifyContent="flex-end">
          <Typography>After Discount Album Total :</Typography>
          <Typography color="green" sx={{ textAlign: 'right', width: 120 }}>
            {fCurrency(data?.countryzone?.currency_sign, Math.round(orderData?.perAlbumCostDiscount)) || '-'}
          </Typography>
        </Stack>
        <Stack direction="row" justifyContent="flex-end">
          <Typography>Total Album Price :</Typography>
          <Typography sx={{ textAlign: 'right', width: 120 }}>
            {fCurrency(data?.countryzone?.currency_sign, Math.round(orderData?.totaleAlbumCost)) || '-'}
          </Typography>
        </Stack>

        <Stack direction="row" justifyContent="flex-end">
          <Typography>Design charges {fCurrency(data?.countryzone?.currency_sign, data?.pritnig_price)}:</Typography>
          <Typography
            color="green"
            sx={{ textAlign: 'right', width: 120 }}
          >
            {`${fCurrency(data?.countryzone?.currency_sign, (data?.pritnig_price *
              data?.page_qty))}`}
          </Typography>
        </Stack>
        <Stack direction="row" justifyContent="flex-end">
          <Typography>Photo Book Copies {fCurrency(data?.countryzone?.currency_sign, data?.album_book_copy_price)}:</Typography>
          <Typography
            color="green"
            sx={{ textAlign: 'right', width: 120 }}
          >
            {`${fCurrency(data?.countryzone?.currency_sign, (data?.album_book_copy_price * data?.album_book_copy_qty))}`}
          </Typography>
        </Stack>
        <Stack direction="row" justifyContent="flex-end">
          <Typography>Subtotal :</Typography>
          <Typography sx={{ textAlign: 'right', width: 120 }}>
            {fCurrency(data?.countryzone?.currency_sign, Math.round(orderData?.subtotale)) || '-'}
          </Typography>
        </Stack>

        <Stack direction="row" justifyContent="flex-end">
          <Typography>Shiping Charges :</Typography>
          <Typography sx={{ textAlign: 'right', width: 120 }}>
            {data?.shippingValue ? fCurrency(data?.countryzone?.currency_sign, data?.shippingValue) : '-'}
          </Typography>
        </Stack>

        <Stack direction="row" justifyContent="flex-end">
          <Typography variant="h6">Final Price :</Typography>
          <Typography variant="h6" sx={{ textAlign: 'right', width: 120 }}>
            {fCurrency(data?.countryzone?.currency_sign, Math.round(orderData?.subtotale + data?.shippingValue)) || '-'}
          </Typography>
        </Stack>
      </Stack>
      <Stack justifyContent="flex-end" direction="row" spacing={2} sx={{ mt: 3 }}>
        <LoadingButton
          color="success"
          size="large"
          variant="contained"
          loading={loadingSave}
          sx={{ width: 250 }}
          onClick={() => {
            setLoadingSave(true)
            api.ordersApi.discount(data?.id, orderData, () => {
              dispatch(fetchOrder())
              setLoadingSave(false)
            })

          }}
        >
          Save Changes
        </LoadingButton>
      </Stack>
    </Box>
  );
}
